.Container {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.Content {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}

.Image {
  width: 100%;
  height: 300px;
  object-fit: cover;
}

@media screen and (min-width: 900px) {
  .Content {
    width: 80%;
  }
}
